import Axios from "axios";
import { useState, useEffect, useRef } from "react";

import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import InputGroup from "react-bootstrap/InputGroup";
import ListGroup from "react-bootstrap/ListGroup";

import { createWorker } from "tesseract.js";

function ScanExpirationDate() {
  const videoRef = useRef(null);
  const photoRef = useRef(null);
  const [hasPhoto, setHasPhoto] = useState(false);

  const [selectedImage, setSelectedImage] = useState(null);
  const [expirationDate, setExpirationDate] = useState("");
  const [resultDay, setResultDay] = useState(0);
  const [resultMonth, setResultMonth] = useState(0);
  const [resultYear, setResultYear] = useState(0);

  const [scanCycle, setScanCycle] = useState(0);

  const getVideo = () => {
    navigator.mediaDevices
      .getUserMedia({
        video: { width: 300, height: 300, facingMode: "environment" },
      })
      .then((stream) => {
        let video = videoRef.current;
        video.srcObject = stream;
        video.play();
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const takePhoto = () => {
    setHasPhoto(true);
    const width = 300;
    const height = 300;

    let video = videoRef.current;
    let photo = photoRef.current;

    photo.width = width;
    photo.height = height;

    setHasPhoto(true);

    let ctx = photo.getContext("2d");
    ctx.drawImage(video, 0, 0, width, height);

    var imgPixels = ctx.getImageData(0, 0, width, height);
    /*
  for (var y = 0; y < height; y++) {
    for (var x = 0; x < width; x++) {
      var i = y * 4 * width + x * 4;
      var avg =
        (imgPixels.data[i] + imgPixels.data[i + 1] + imgPixels.data[i + 2]) /
        3;
      imgPixels.data[i] = avg;
      imgPixels.data[i + 1] = avg;
      imgPixels.data[i + 2] = avg;
    }
  }
  */
    /*
  let threshold = 130; // 0..255

  for (var i = 0; i < imgPixels.data.length; i += 4) {
    // 4 is for RGBA channels
    // R=G=B=R>T?255:0
    imgPixels.data[i] =
      imgPixels.data[i + 1] =
      imgPixels.data[i + 2] =
        imgPixels.data[i + 1] > threshold ? 255 : 0;
  }

  ctx.putImageData(imgPixels, 0, 0, 0, 0, imgPixels.width, imgPixels.height);
  */
    let final = photo.toDataURL();

    //let processedImage = new Image();
    //processedImage.src = final;
    //console.log(final);
    setSelectedImage(final);
    //runTesseract();
  };

  const takeAgain = () => {
    let photo = photoRef.current;

    let ctx = photo.getContext("2d");
    ctx.clearRect(0, 0, photo.width, photo.height);
    setHasPhoto(false);
  };

  useEffect(() => {
    getVideo();
  }, [videoRef]);

  const runTesseract = () => {
    (async () => {
      const worker = await createWorker({
        langPath: "https://tessdata.projectnaptha.com/4.0.0_best",
        cachePath: "https://tessdata.projectnaptha.com/4.0.0_best",
        logger: (m) => console.log(m),
      });
      await worker.loadLanguage("eng+ces");
      await worker.initialize("eng+ces");

      const rectangle = {
        left: 50,
        top: 125,
        width: 200,
        height: 50,
      };

      const {
        data: { text },
      } = await worker.recognize(selectedImage, {
        tessedit_char_whitelist: "0123456789./",
        rotateAuto: true,
        rotateRadians: true,
        rectangle,
      });
      console.log(text);
      var res = text.match(/\d{2}([\/.-])\d{2}\1\d{4}/g);
      console.log(res);

      var newdate = "";
      if (res != null) {
        if (res[0].includes(".") == true) {
          newdate = res[0].toString().split(".");
          console.log("tečka");
          setResultDay(parseInt(newdate[0]));
          setResultMonth(parseInt(newdate[1]));
          setResultYear(parseInt(newdate[2]));
        } else if (res[0].includes("/") == true) {
          newdate = res[0].toString().split("/");
          console.log("carka");
          setResultDay(parseInt(newdate[0]));
          setResultMonth(parseInt(newdate[1]));
          setResultYear(parseInt(newdate[2]));
        } else {
          console.log("NIC PRVNI PODMINKA");
        }
      } else {
        setResultDay(null);
        console.log("NIC");
      }

      //var newdate = myString.toString().split(".");

      //setTextResult(text);

      await worker.terminate();
    })();
  };

  function changeDate(inputType, changeAction) {
    if (inputType == "days") {
      if (changeAction == "increase") {
        setResultDay(resultDay + 1);
      } else if (changeAction == "decrease") {
        setResultDay(resultDay - 1);
      }
    } else if (inputType == "months") {
      if (changeAction == "increase") {
        setResultMonth(resultMonth + 1);
      } else if (changeAction == "decrease") {
        setResultMonth(resultMonth - 1);
      }
    } else if (inputType == "years") {
      if (changeAction == "increase") {
        setResultYear(resultYear + 1);
      } else if (changeAction == "decrease") {
        setResultYear(resultYear - 1);
      }
    }
  }

  const saveExpirationDate = () => {
    setExpirationDate(
      resultDay.toString() +
        "." +
        resultMonth.toString() +
        "." +
        resultYear.toString()
    );
  };
  return (
    <div className="App">
      <Container fluid>
        <Row>
          <Col>
            <div className="camera">
              <div className="rectangle"></div>
              <div className="overlay"></div>
              <video
                className={hasPhoto ? "" : "videoShow"}
                ref={videoRef}
              ></video>
              <canvas
                className={hasPhoto ? "canvasShow" : ""}
                ref={photoRef}
              ></canvas>
            </div>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col className=" d-grid gap-2">
            <Button variant="primary" type="button" onClick={takePhoto}>
              <i className="fa-solid fa-camera fa-beat-fade"></i>
              <br></br> Photo
            </Button>
          </Col>
          <Col className="d-grid gap-2">
            <Button variant="primary" type="button" onClick={takeAgain}>
              <i className="fa-solid fa-repeat"></i>
              <br></br> Take Again
            </Button>
          </Col>
          <Col className="d-grid gap-2">
            <Button variant="primary" type="button" onClick={runTesseract}>
              <i className="fa-solid fa-barcode"></i>
              <br></br>Run Scanner
            </Button>
          </Col>
        </Row>
      </Container>
      {resultDay != 0 && (
        <Container fluid>
          <Row>
            <Col>
              <h3 className="text-center mt-3">Is the date correct?</h3>
            </Col>
          </Row>
          <Row>
            <Col>
              <h6 className="text-center">You can adjust it below</h6>
            </Col>
          </Row>
          <Row>
            <Form className="mt-3">
              <Row>
                <Col>
                  <InputGroup>
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("days", "decrease")}
                    >
                      <i className="fa-solid fa-minus"></i>
                    </Button>
                    <Form.Control
                      type="number"
                      value={resultDay}
                      placeholder="Days"
                    />
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("days", "increase")}
                    >
                      <i className="fa-solid fa-plus"></i>
                    </Button>
                  </InputGroup>
                </Col>
                <Col>
                  <InputGroup>
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("months", "decrease")}
                    >
                      <i className="fa-solid fa-minus"></i>
                    </Button>
                    <Form.Control
                      type="number"
                      value={resultMonth}
                      placeholder="Days"
                    />
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("months", "increase")}
                    >
                      <i className="fa-solid fa-plus"></i>
                    </Button>
                  </InputGroup>
                </Col>
                <Col>
                  <InputGroup>
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("years", "decrease")}
                    >
                      <i className="fa-solid fa-minus"></i>
                    </Button>
                    <Form.Control
                      type="number"
                      value={resultYear}
                      placeholder="Days"
                    />
                    <Button
                      variant="primary"
                      type="button"
                      size="sm"
                      onClick={() => changeDate("years", "increase")}
                    >
                      <i className="fa-solid fa-plus"></i>
                    </Button>
                  </InputGroup>
                </Col>
              </Row>
            </Form>
          </Row>
          <Row className="text-center">
            <Col>
              <Button
                className="mt-3"
                variant="success"
                type="button"
                onClick={saveExpirationDate}
              >
                <i className="fa-solid fa-save"></i> Save the Date
              </Button>
            </Col>
          </Row>
        </Container>
      )}
      {resultDay === null && (
        <Container fluid>
          <Row className="text-center">
            <Col>
              <h5>Date was not recognized</h5>
            </Col>
          </Row>
        </Container>
      )}
      {expirationDate && (
        <Container fluid>
          <Row className="mt-3">
            <Col>
              <ListGroup>
                <ListGroup.Item>{expirationDate}</ListGroup.Item>
              </ListGroup>
            </Col>
          </Row>
        </Container>
      )}
    </div>
  );
}

export default ScanExpirationDate;
